.switch {
  position: relative;
  z-index: 1;
  width: 100%;
  height: 4.375vw;
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  font-size: 1.66667vw;
  line-height: 1.66667vw;
  text-transform: uppercase;
  border-radius: 2.60417vw;
  color: #ffffff;
  background: linear-gradient(106.16deg, #009ec1 0%, #25d6fd 18.48%, #27e4db 85.75%, #04fff3 98.59%);
  overflow: hidden; }
  @media only screen and (max-width: 1439px) {
    .switch {
      font-size: 1.38889vw;
      line-height: 1.38889vw; } }
  @media only screen and (max-width: 1079px) {
    .switch {
      height: 6.52174vw;
      font-size: 2.89855vw;
      line-height: 2.89855vw;
      border-radius: 12.07729vw; } }
  @media only screen and (max-width: 767px) {
    .switch {
      height: 13.04348vw;
      font-size: 4.34783vw;
      line-height: 4.34783vw;
      border-radius: 12.07729vw; } }
  .switch input {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 3;
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer; }
    .switch input:checked ~ .switch__toggler {
      transform: translateX(-42%); }
  .switch__toggler {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 172%;
    height: 100%;
    padding: 0 3.64583vw;
    transition: 0.3s ease-in-out; }
    .switch__toggler-item {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 4.01042vw;
      height: 4.01042vw;
      border-radius: 50%;
      background: radial-gradient(65.4% 65.4% at 50.04% 50.01%, #f8f8f8 0%, #f9f9f9 55.89%, #f7f7f7 71.71%, #f3f3f3 79.42%, #f3f3f3 80.03%); }
      @media only screen and (max-width: 1079px) {
        .switch__toggler-item {
          width: 5.07246vw;
          height: 5.07246vw; } }
      @media only screen and (max-width: 767px) {
        .switch__toggler-item {
          width: 11.11111vw;
          height: 11.11111vw; } }

